import React from "react"
import styled from "styled-components"

import { Container } from "../Styles/Container"
import { primaryColor } from "../Styles/Global"

const Project = styled.figure`
  margin: 10px 0;
  overflow: hidden;
  position: relative;
  max-height: 300px;
  @media (min-width: 800px) {
    margin: 0px;
    &:hover > figcaption {
      transform: translateX(0);
    }
  }
`
const ProjectThumbnail = styled.img`
  width: 100%;
  height: 100%;
  display: block;
  object-fit: cover;
`
const ProjectContent = styled.figcaption`
  padding: 10px 0;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  background: #fff;
  transform: translateY(0);
  transition: transform 0.4s ease;
  @media (min-width: 800px) {
    top: 40%;
    width: 55%;
    height: 20%;
    bottom: initial;
    transform: translateY(-40%);
    transform: translateX(-100%);
    display: flex;
    align-items: center;
    p {
      width: 100%;
      border-left: none;
    }
  }
`
const ProjectTitle = styled.p`
  color: ${primaryColor};
  margin: 0;
  font-size: 1rem;
  font-weight: bold;
  text-align: center;
  line-height: 30px;
  border-left: solid 5px ${primaryColor};
  border-right: solid 5px ${primaryColor};
  padding-left: 0.8rem;
  text-transform: capitalize;
  letter-spacing: 1px;
  @media (min-width: 800px) {
    font-size: 1.3rem;
    font-weight: 400;
  }
`

const FlexContainer = Container.extend`
  padding-top: 80px;
  padding-bottom: 80px;
  display: flex;
  flex-wrap: wrap;
  > * {
    flex: 1 1 100%;
  }
  @media (min-width: 800px) {
    > * {
      flex-basis: 50%;
    }
  }
`

const Projects = ({ projects }) => {
  if (projects) {
    var projectsList = projects.map((project, i) => (
      <Project key={i}>
        <a href={project.url} target="_blank" rel="noopener noreferrer">
          <ProjectThumbnail src={`./images/${project.thumbnail}`} />
        </a>
        <ProjectContent>
          <ProjectTitle>{project.name}</ProjectTitle>
        </ProjectContent>
      </Project>
    ))
  }
  return (
    <section style={{ background: `${primaryColor}` }}>
      <FlexContainer>{projectsList}</FlexContainer>
    </section>
  )
}

export default Projects
